.contain{
    text-align: center;
    color: white;


    h2.header{
        font-size: 5vw;
        opacity: 0;
        padding: 5px;
    }
    .show{
        transform: translateY(-50%);
        animation: pop 1s ease-in forwards;
    }

    .p-top{
        padding: 10px 0px;
        opacity: 0;
    }

    .respomsive{
        opacity: 0 ;

    }
    .d-flex{
        display: flex;

    }
    .active{
        animation: popAsideLeft 1s .5s ease-in-out forwards;
    }
    .activeRight{
        animation: popAsideRight .5s  ease-in-out forwards;
    }
    .section-center{
        margin: 20px auto;
        width: 80%;
        justify-content: center;
    }
    .dexcription{
        text-align: left;
        width : 30vw;
        width: 50vw;
        padding-left: 20px;
        padding-top: 15px;
        font-size: 1.5vw;
        opacity: 0;
    }
    ul{
        list-style: none;

    }
    li{
        padding-left: 25px;
        position: relative;
    }
    .Left__icon{
        position: absolute;
        top : 0;
    }
}


@keyframes pop {
    0% {
        transform: scale(.5);
        opacity: 0;
        transform: translateY(-30%)

    }
    5%{

    }
    60%{
        transform: scale(1.3);
    }

    100%{
        transform: scale(1);
        transform: translateY(0%);

        opacity: 1;
    }
}


// popAsideLeft
@keyframes popAsideLeft {
    0% {
        opacity: 0;
        transform: translateX(-50%)
    }

    60%{
        transform: translateX(-30%);
    }

    100%{
        transform: translateX(0%);
        opacity: 1;
    }
}


// popAsideRight
@keyframes popAsideRight {
    0% {
        opacity: 0;
        transform: translateX(60%)
    }

    60%{
        transform: translateX(30%);
    }

    100%{
        transform: translateX(0%);
        opacity: 1;
    }
}




// ** MEDIA QURIES **

@media (min-width : 0px){
    .section-center{
        flex-wrap: wrap;
    }
    .respomsive{
        height: 30vh;
        width: 40vw;
    }
    .Left__icon{
        left: 5vw;
    }
}
@media (min-width : 320px){
    .section-center{
        flex-wrap: wrap;
    }

    .respomsive{
        height: 30vh;
        width: 40vw;
    }
    .Left__icon{
        left: 3.8vw;
    }

}
@media (min-width : 480px){
    .section-center{
        flex-wrap: nowrap;
    }
    .respomsive{
        height: 40vh;

    }
    .Left__icon{
        left: 2vw;
    }
}
@media (min-width : 600px){
    .Left__icon{
        left: 1.5vw;
    }
}
@media (min-width : 768px){
    .Left__icon{
        left: 1.2vw;
    }
}
@media (min-width : 900px){
    .Left__icon{
        left: 2px;
    }
}
@media (min-width : 1024px){
    
}
@media (min-width : 1200px){
    .respomsive{
        height: 50vh;
        width: 30vw;
    }
    .Left__icon{
        left: 5px;
    }
}