.intro{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: white;
    text-align: center;
    z-index: 2;
}


.time-intro , .dev-job{
    font-size: 1.25vw;
}
.time-intro{
    animation: effect 2.5s ease-in-out;
}
.dev-name{
    font-size:4vw
}

.Dev_ani{
    animation: lightOn 3s  ease-in-out;
    animation-fill-mode: forwards;
}
.Dev_NotAni{
    color: rgb(107, 81, 71);
    min-height: 25px;
}
.dev-job{
    opacity: 0;
    transform: translateY(200%);
    transition: transform 1.5s, opacity 1.5s;
}
.devJobTrans{
    transform: translateY(0%);
    opacity: 1;

}


@keyframes effect{
    0%{
        transform: translateY(-250%);
        opacity: 0;
    }
    100%{
        transform: translateY(0%);
        opacity: 1;
    }
}


@keyframes lightOn{
    0%{
        color: rgb(255, 137, 94);
    }
    20%{
        color: rgb(77, 69, 67);
    }
    35%{
        color: rgb(255, 137, 94);
    }
    39%{
        color: rgb(77, 69, 67);
    }   
     45%{
        color: rgb(255, 137, 94);
    }    
    55%{
        color: rgb(77, 69, 67);
    }
    100%{
        color: rgb(255, 137, 94);

    }
}


