.Container{
    color : white;
    text-align: center;
    padding : 30px;
    h2.header{
        font-size : 5vw ; 

    }
    .red{
        color : red;
        padding: 30px;
        font-size : 2rem ;
        cursor: pointer;

    }
    .red:hover{
        color :#ddd;
    }
    p.header{
        font-size : 1.5vw ; 

    }
    .d-flex{
        display: flex;
    }
    .section-container{
        justify-content: space-evenly;
        align-items: flex-end;
        width: 80%;
        margin: 0px auto;
        min-height: 40vh;

    }
    .section-container > div{
        flex: 1 1 40%;

    }

}




// ** MEDIA QURIES **

@media (min-width : 0px){
    .section-container{
        flex-wrap: wrap;
        font-size: 2vw;
    }
}
