.nav{
    background: black;
    padding: 0px 2vw;
    font-size: 3vw;

    ul.NavLinks{
        margin-right : auto;
        display: flex;
        flex-wrap: wrap;
    }
    li.navLinksContainer{
        padding: 2vh 3vw;
        list-style: none;
    }

    li a.nav-items{
        color: white;
        text-decoration: none;
    }


}
// ** MEDIA QURIES **

@media (min-width : 0px){
    .nav{
        font-size: 3.5;
    }
}
@media (min-width : 320px){
    .nav{
        font-size: 3.2vw;
    }
}
@media (min-width : 480px){
    .nav{
        font-size: 3vw;
    }
}
@media (min-width : 600px){
    .nav{
        font-size: 2vw;
    }
}
