.container{
    color : white;
    text-align: center;

    h2.header{
        font-size: 5vw;
        opacity: 0;
        padding: 5px;
    }
    p.header{
        font-size: 1.5vw;
        opacity: 0;
        padding: 5px;
        padding-bottom: 50px;
    }
    .expma{
        padding-bottom: 5vh;
    }
    .expma h3{
        padding: 30px;
    }
    .show{
        transform: translateY(-50%);
        animation: pop 1s ease-in forwards;
        perspective: 500px;
    }
    .responsize{
        width: 70vw;
        height: 80vh;
    }
    .responsize:hover{
        transform: scale(1.01);
        box-shadow: 0 0 10px 10px white;
    }
    .PW{
        opacity: 0;
        transform : rotateY(120deg);
        animation: rotat 1.5s ease-in-out forwards;
    }

    // rotation animation 
    @keyframes rotat {
        0%{
            opacity : 0 ; 
            transform : rotateY(120deg);
        }
        100%{
            opacity : 1 ; 
            transform : rotateY(0deg);
        }

    }



    //pop animatiion
    
    @keyframes pop {
        0% {
            transform: scale(.5);
            opacity: 0;
            transform: translateY(-30%)
    
        }
        5%{
    
        }
        60%{
            transform: scale(1.3);
        }
    
        100%{
            transform: scale(1);
            transform: translateY(0%);
    
            opacity: 1;
        }
    }
}
